@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
}

footer {
  display: flex;
  align-items: center;
  justify-content: center;
}

footer > p {
  font-size: 24px;
}

.onhover_hide {
  opacity: 0;
}

.onhover_show {
  opacity: 1;
}

.notclickedBtn {
  visibility: hidden;
}

.clickedBtn {
  visibility: visible;
  color: green;
  animation: fade 2s ease-out forwards;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}